body {
  overflow-x: hidden;
}

@keyframes grow {
  0% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}

.contact_container {
  width: 100%;
  min-height: 100%;
  max-height: max-content;
  display: flex;
  align-items: center;
  justify-content: center;
  /*  animation-name: grow; */
  animation-duration: 0.8s;
  overflow-y: auto;
  font-size: 18px;
}
.message {
  text-align: center;
  font-size: 14px;
  color: grey;
  line-height: 1.3em;
  padding-bottom: 24px;
}

.contact_form {
  margin: 2% 0;
  height: max-content;
  min-width: 38%;
  border-radius: 6px;
  text-align: center;
  box-sizing: border-box;
  background-color: white;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.12);
}

button:hover {
  opacity: 0.8;
}

.errorMsg {
  position: relative;
  font-size: 12px;
  color: #da1e1e;
  text-align: left;
  padding-left: 13%;
  padding-bottom: 10px;
  /* margin: 0px; */
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  width: 80%;
  padding: 12px 8px;
  margin-bottom: 8px;
  display: inline-block;
  border: 1px solid #111;
  border-radius: 4px;
  box-sizing: border-box;
}
textarea {
  height: 150px;
  overflow-y: auto;
  font-family: Arial, sans-serif;
  max-width: 80%;
  min-width: 80%;
}

button[type="submit"] {
  padding: 10px 24px;
  color: white;
  font-weight: 600;
  background-color: #003049;
  border: none;
  border-radius: 5px;
  margin: 24px 0 48px;
}
.contact_title {
  font-size: 24px;
  font-weight: 600;
  color: #003049;
  line-height: 1em;
  padding: 32px 0 10px;
  margin-top: 5%;
}

/* On success: */
.msg_sent {
  position: absolute;
  top: 17%;
  height: 60%;
  width: 45%;
  background-color: white;
  border: 1px solid #1f2e60;
  color: #111;
  border-radius: 8px;
  text-align: center;
  box-sizing: border-box;
  overflow-y: auto;

  font-size: 22px;
  line-height: 1.5em;
}
.msg_icon {
  padding-top: 10%;
}

.error_img {
  height: 100px;
}

button.backHome {
  padding: 10px 15px;
  font-size: 20px;

  font-weight: normal;
  color: #1f2e60;
  border: 1px solid #1f2e60;
  border-radius: 8px;
  background-color: transparent;
  margin-top: 30px;
  cursor: pointer;
}

@media only screen and (max-width: 799px) {
  .contact_form {
    width: 100%;
    margin: 0;
  }
}

@media only screen and (min-width: 800px) and (max-width: 1220px) {
  .contact_form {
    min-width: 60%;
  }
}

@media only screen and (min-width: 1800px) {
  .contact_title {
    /* margin-top: 25%; */
    font-size: 36px;
  }
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  textarea {
    font-size: 24px;
  }
  textarea {
    height: 280px;
  }

  .errorMsg {
    font-size: 22px;
  }

  button[type="submit"] {
    font-size: 26px;
  }

  .msg_sent {
    font-size: 36px;
  }

  button.backHome {
    padding: 15px 20px;
    font-size: 30px;
    margin-top: 140px;
  }
}

@media only screen and (min-width: 2200px) {
  .msg-sent {
    font-size: 42px;
  }
  button.backHome {
    padding: 20px 25px;
    font-size: 36px;
    margin-top: 50px;
  }
}
